<template>
    <div>
        <StandardHeader></StandardHeader>
    </div>
</template>

<script>
// Library components.
// Custom components.
import StandardHeader from './StandardHeader.vue'

export default {
    name: 'Home',
    components: {
        StandardHeader
    },

    data() {
        return {
            loading: false
        }
    },
    created: function() {
        this.fetch()
    },
    watch: {
        '$route': 'fetch'
    },
    methods: {
        fetch(page = 1) {
            console.log('fetch')
        }
    }
}
</script>

